.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body
{
    font-family: 'Protest Riot', sans-serif; 
    background: rgb(238,174,202);
    background: radial-gradient(circle, rgba(238,174,202,1) 0%, rgba(255,208,208,1) 48%);
}

#picture
{
  height: 500px;
  margin-bottom: 2rem;
}

.container
{
    display: grid;
    justify-content: center;
}

.buttonContainer
{
  font-family: 'Protest Riot', sans-serif;
  padding-left: 4rem;
  padding-right: 4rem;
  display: flex;
  justify-content: center;
}

button
{
  width: 8rem;
  height: 4rem;
  cursor: pointer;
  font-size: 1.5rem;
  font-family: 'Protest Riot', sans-serif;
}

.yes
{
  margin-right: 1.5rem;
}

.no
{
  margin-left: 1.5rem;
}